const reducer = (state= intialState, action) => {
    switch (action.type){
        case LOG_IN_REQUEST: {  //로그인 성공시
            return {
                ...state,
                isLoggingIn:true,
                logInErrorReason: '',
            }
        }
        case LOG_IN_SUCCESS: {
            return {
                ...state,
                isLoggingIn:false,
                isLoggedIn:true,
                me:dummyUser,
                isLoading: false
            }
        }
        case LOG_IN_FAILURE: {
            return {
                ...state,
                isLoggingIn:false,
                isLoggedIn:false,
                logInErrorReason: action.error,
                me: null,
            }
        }
        case LOG_OUT_REQUEST: {
            return {
                ...state,
                isLoggedIn: false,
                me: null,
            }
        }
        case SIGN_UP_REQUEST: {
            return {
              ...state,
              isSigningUp: true,
              isSignedUp: false,
              signUpErrorReason: '',
            };
          }
          case SIGN_UP_SUCCESS: {
            return {
              ...state,
              isSigningUp: false,
              isSignedUp: true,
            };
          }
          case SIGN_UP_FAILURE: {
            return {
              ...state,
              isSigningUp: false,
              signUpErrorReason: action.error,
            };
          }
        default: {
            return {
                ...state,
            }
        }
    }

}

export default reducer;